import {
  moveEvent,
  endEvent,
  getClientPosition,
  isOutOfBrowser,
  getSize,
  getPositionOffset,
  setSize,
  setPositionOffset,
  isInMoveHandler,
  isInMaximizeHandler,
} from './common';

const RESIZE_SCOPE = 10;

function _isOnOtherHandler(el, { moveHandler, maximizeHandler }) {
  return el === moveHandler || el === maximizeHandler;
}

function _setCursor(window, el, positionType) {
  let cursor;
  switch (positionType) {
    case 'top':
    case 'bottom':
      cursor = 'n-resize';
      break;

    case 'left':
    case 'right':
      cursor = 'e-resize';
      break;

    case 'left-top':
      cursor = 'nw-resize';
      break;

    case 'left-bottom':
      cursor = 'sw-resize';
      break;

    case 'right-top':
      cursor = 'ne-resize';
      break;

    case 'right-bottom':
      cursor = 'se-resize';
      break;
  }
  window.style.cursor = cursor;
}

function _resetCursor(window) {
  window.style.cursor = '';
}

function _judgeResizeType(startPoint, target) {
  const x = startPoint.x;
  const y = startPoint.y;
  const offsetTop = target.offsetTop;
  const offsetLeft = target.offsetLeft;
  const offsetWidth = target.offsetWidth;
  const offsetHeight = target.offsetHeight;
  // console.log(
  //   `x:${x};y:${y};offsetTop:${offsetTop}offsetLeft:${offsetLeft}offsetWidth:${offsetWidth}offsetHeight:${offsetHeight}`
  // );
  if (Math.abs(offsetLeft - x) <= RESIZE_SCOPE) {
    if (Math.abs(offsetTop - y) <= RESIZE_SCOPE) {
      return 'left-top';
    } else if (Math.abs(offsetTop + offsetHeight - y) <= RESIZE_SCOPE) {
      return 'left-bottom';
    } else {
      return 'left';
    }
  }

  if (Math.abs(offsetLeft + offsetWidth - x) <= RESIZE_SCOPE) {
    if (Math.abs(offsetTop - y) <= RESIZE_SCOPE) {
      return 'right-top';
    } else if (Math.abs(offsetTop + offsetHeight - y) <= RESIZE_SCOPE) {
      return 'right-bottom';
    } else {
      return 'right';
    }
  }

  if (Math.abs(offsetTop - y) <= RESIZE_SCOPE) {
    return 'top';
  } else if (Math.abs(offsetTop + offsetHeight - y) <= RESIZE_SCOPE) {
    return 'bottom';
  }

  return false;
}

function _calWidthAndOffset({
  type,
  originSize,
  originOffset,
  nowPosition,
  startPoint,
  minWidth,
  maxWidth,
  minHeight,
  maxHeight,
}) {
  /* / */
  function _getLimitWidth({ minWidth, maxWidth, currentWidth }) {
    /*  */
    if (!!minWidth && currentWidth < minWidth) {
      currentWidth = minWidth;
    } else if (currentWidth < 0) {
      currentWidth = 0;
    }
    /*  */
    if (!!maxWidth && currentWidth > maxWidth) {
      currentWidth = maxWidth;
    }

    return currentWidth;
  }
  /* / */
  function _getLimitHeight({ minHeight, maxHeight, currentHeight }) {
    /*  */
    if (!!minHeight && currentHeight < minHeight) {
      currentHeight = minHeight;
    } else if (currentHeight < 0) {
      currentHeight = 0;
    }
    /*  */
    if (!!maxHeight && currentHeight > maxHeight) {
      currentHeight = maxHeight;
    }

    return currentHeight;
  }

  const optionWidth = nowPosition.x - startPoint.x + originSize.width;
  const optionHeight = nowPosition.y - startPoint.y + originSize.height;
  let calWidth = originSize.width;
  let calHeight = originSize.height;
  let calTop = originOffset.y;
  let calLeft = originOffset.x;
  /*  */
  if (type.indexOf('left') > -1) {
    calWidth = startPoint.x - nowPosition.x + originSize.width; // 
    calWidth = _getLimitWidth({ minWidth, maxWidth, currentWidth: calWidth }); // /
    calLeft = originOffset.x - (calWidth - originSize.width); // left
  }
  /*  */
  if (type.indexOf('top') > -1) {
    calHeight = startPoint.y - nowPosition.y + originSize.height; // 
    // /
    calHeight = _getLimitHeight({
      minHeight,
      maxHeight,
      currentHeight: calHeight,
    });
    calTop = originOffset.y - (calHeight - originSize.height); // top
  }
  /*  */
  if (type.indexOf('right') > -1) {
    calWidth = _getLimitWidth({
      minWidth,
      maxWidth,
      currentWidth: optionWidth,
    });
  }
  /*  */
  if (type.indexOf('bottom') > -1) {
    calHeight = _getLimitHeight({
      minHeight,
      maxHeight,
      currentHeight: optionHeight,
    });
  }

  return { calWidth, calHeight, calTop, calLeft };
}

/**
 * 
 * windowmoveEventdocument
 *
 * @param {Event} event
 * @returns
 */
export function handleStartEventForResize(startEvent) {
  function _handleMoveEventForResize(moveEvent) {
    /* window */
    if (isOutOfBrowser(moveEvent)) {
      return false;
    }
    const nowPosition = getClientPosition(moveEvent); // /
    const { calWidth, calHeight, calTop, calLeft } = _calWidthAndOffset({
      type,
      originSize,
      originOffset,
      nowPosition,
      startPoint,
      minWidth: params.minWidth,
      maxWidth: params.maxWidth,
      minHeight: params.minHeight,
      maxHeight: params.maxHeight,
    });
    setSize(target, calWidth, calHeight);
    setPositionOffset(target, calLeft, calTop);
    moveEvent.stopPropagation();
  }

  function _handleEndEventForResize(endEvent) {
    document.removeEventListener(moveEvent, _handleMoveEventForResize, false); // 

    endEvent.preventDefault();
    endEvent.stopPropagation();
  }

  const eventEl = startEvent.target;
  const params = this.params;
  const target = this.window;
  const startPoint = getClientPosition(startEvent); // 
  const type = _judgeResizeType(startPoint, target); // 
  /*  */
  if (!type) {
    return;
  }
  /* handler */
  if (isInMoveHandler(eventEl, params)) {
    return;
  }
  /* handler */
  if (isInMaximizeHandler(eventEl, params)) {
    return;
  }
  let originSize = getSize(target);
  originSize = {
    width: parseInt(originSize.width),
    height: parseInt(originSize.height),
  };

  const originOffset = getPositionOffset(target);

  document.addEventListener(moveEvent, _handleMoveEventForResize, false); // 
  document.addEventListener(endEvent, _handleEndEventForResize); // endEvent
  startEvent.preventDefault();
  startEvent.stopPropagation();
}

export function cursorChange(event) {
  const target = this.window;
  const currentPoint = getClientPosition(event); // 
  const type = _judgeResizeType(currentPoint, target); // 

  /* cursor */
  if (_isOnOtherHandler(event.target, this) || !type) {
    _resetCursor(this.window);
  } else {
    _setCursor(this.window, event.target, type);
  }
}
